import React, {Component} from 'react';
import Styles from './WaitOverlay.module.css';
import Loading from './../../../Assets/Images/loading.gif'

export default class WaitOverlay extends Component {
    state = {
        message: 0
    };

    interval = null;

    constructor() {
        super();
        this.newMessage = this.newMessage.bind(this);
    }

    componentDidMount(): void {
        this.newMessage();
    }

    componentWillUnmount(): void {
        clearInterval(this.interval);
    }

    messages = [
        'Chyba miaem co zrobi...',
        'Shrek, daleko jeszcze?',
        'Szukanie Nemo...',
        'Prosz czeka, mae elfy rysuj Twj obrazek',
        'Par bitw prbowao uciec, ale zapalimy je.',
        'Ustawianie satelity...',
        'Bity pyn dzi wyjtkowo wolno.',
        'Po prostu policz do 10.',
        'Pieczenie ciastka.',
        'Obliczanie szansy na powodzenie operacji...',
        'Yyy, czy to Windows?',
        'Przysigam, ju prawie skoczylimy!',
        'adna dzi pogoda, prawda?',
        'Czekanie a farba wyschnie...',
        'adowanie moduu adowania...',
        'Mw mi Steve.',
        'To nie tak proste jak wyglda.',
        'Do zrobienia: Puci muzyk z windy.',
        'Robienie niemoliwego.',
        'Negocjowanie ze SkyNet\'em.',
        'Czekanie na pocig do Hogwartu...',
        'Podchodzenie do przekanika masy...',
        'ciganie Czarnej Pery...'
    ];

    newMessage() {
        this.setState({message: Math.floor(Math.random() * this.messages.length)});
    }

    render() {
        if (!this.props.visible) {
            if (this.interval !== null) {
                clearInterval(this.interval);
            }
            return null;
        } else {
            if (this.interval === null) {
                this.interval = setInterval(this.newMessage, 3500);
            }
        }

        return (
            <div className={Styles.background}>
                <img src={Loading} alt=""/>
                <p>{this.messages[this.state.message]}</p>
            </div>
        )
    }
}